import React, { useState } from "react";
import { useLocation } from "react-router";
import {
    experimentalStyled,
    useMediaQuery,
    Container,
    Box,
} from "@mui/material";
import { Outlet } from "react-router-dom";
import Header from "./Header/Header";
import Sidebar from "./Sidebar/Sidebar";
import Footer from "./Footer/Footer";
import { TopbarHeight } from "../../assets/global/Theme-variable";
import useLocalStorage from "../../hooks/useLocalStorage";

const MainWrapper = experimentalStyled("div")(({ theme }) => ({
    display: "flex",
    minHeight: "100vh",
    overflow: "hidden",
    width: "100%",
}));
const PageWrapper = experimentalStyled("div")(({ theme }) => ({
    display: "flex",
    flex: "1 1 auto",
    overflow: "hidden",

    backgroundColor: theme.palette.background.default,
    [theme.breakpoints.up("lg")]: {
        paddingTop: TopbarHeight,
    },
    [theme.breakpoints.down("lg")]: {
        paddingTop: "64px",
    },
}));

const FullLayout = () => {
    //
    const [isSidebarOpen, setSidebarOpen] = useState(true);
    const [searchOpen, setSearchOpen] = React.useState(false);
    const [isMobileSidebarOpen, setMobileSidebarOpen] = useState(false);
    const lgUp = useMediaQuery((theme) => theme.breakpoints.up("lg"));

    const { value: autoCheckinActivated, setValue: setAutoCheckinActivated } =
        useLocalStorage("autoCheckinActivated", false);
    const {
        value: autoBadgePrintActivated,
        setValue: setAutoBadgePrintActivated,
    } = useLocalStorage("autoBadgePrintActivated", false);

    const { pathname } = useLocation();

    return (
        <MainWrapper>
            <Header
                sx={{
                    paddingLeft:
                        isSidebarOpen &&
                        lgUp &&
                        !pathname.includes("selfService")
                            ? "300px"
                            : "",
                    backgroundColor: "#ffffff",
                }}
                toggleSidebar={() => setSidebarOpen(!isSidebarOpen)}
                toggleMobileSidebar={() => setMobileSidebarOpen(true)}
                autoCheckinActivated={autoCheckinActivated}
                autoBadgePrintActivated={autoBadgePrintActivated}
                setSearchOpen={setSearchOpen}
            />

            <Sidebar
                isSidebarOpen={
                    pathname.includes("selfService") ? false : isSidebarOpen
                }
                isMobileSidebarOpen={isMobileSidebarOpen}
                setMobileSidebarOpen={setMobileSidebarOpen}
                onSidebarClose={() => setMobileSidebarOpen(false)}
            />

            <PageWrapper>
                <Container
                    maxWidth={false}
                    sx={{
                        paddingTop: "20px",
                        paddingLeft:
                            isSidebarOpen &&
                            lgUp &&
                            !pathname.includes("selfService")
                                ? "300px!important"
                                : "",
                    }}>
                    <Box sx={{ minHeight: "calc(100vh - 170px)" }}>
                        <Outlet
                            context={{
                                autoCheckinActivated,
                                setAutoCheckinActivated,
                                autoBadgePrintActivated,
                                setAutoBadgePrintActivated,
                                searchOpen,
                                setSearchOpen,
                            }}
                        />
                    </Box>
                    <Footer />
                </Container>
            </PageWrapper>
        </MainWrapper>
    );
};

export default FullLayout;
